export default {
  data() {
    return {
      // 手机号码输入的内容
      telVal: "",
      // 验证码输入的内容
      codeVal: "",
      // 点击发送的内容
      sendVal: "点击发送",
      // 倒计时内容
      time: 60,
      // 控制剩余时间的显示与隐藏
      isTime: false,
      // 控制点击发送的显示与隐藏
      isStart: true,
      // 返回的验证码
      ObtainCode: ""
    };
  },
  methods: {
    // 发送验证码
    getUpdatePhone() {
      uni.request({
        url: "http://192.168.1.143:8086/WNC/user/generatePhoneCode",
        data: {
          phone: this.telVal
        },
        header: {
          "Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
        },
        success: res => {
          this.ObtainCode = res.data;
          // console.log(res);
          // 如果res.data 没有收到 需要需要一个弹出框提醒用户
          if (res === "") {
            uni.showToast({
              title: "请输入正确的验证码",
              duration: 2000,
              icon: "none"
            });
          }
        }
      });
    },
    // 点击提交按钮
    submitVal() {
      if (
        this.telVal !== "" &&
        this.codeVal !== "" &&
        this.codeVal !== this.ObtainCode
      ) {
        uni.request({
          url: "http://192.168.1.143:8086/WNC/user/updatePhone",
          data: {
            phone: this.telVal,
            userId: 1,
            code: this.codeVal
          },
          header: {
            "Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
          },
          success: res => {
            // console.log(res);
          }
        });
        uni.showToast({
          title: "更换成功",
          duration: 2000,
          icon: "none",
          success: res => {
            setTimeout(() => {
              uni.navigateBack({
                url: "/pages/mineUserInformation/mineUserInformation"
              });
            }, 2000);
          }
        });
      } else {
        uni.showToast({
          title: "请输入手机号码或验证码",
          duration: 2000,
          icon: "none"
        });
      }
    },
    start() {
      this.isTime = !this.isTime;
      this.isStart = !this.isStart;
    }
  },
  computed: {
    isActive() {
      if (this.telVal === "" || this.codeVal === "") {
        return false;
      }
      return true;
    }
  }
};
